import { HamburgerIcon } from "@chakra-ui/icons";
import {
  Box,
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  Heading,
  HStack,
  useDisclosure,
  useMediaQuery,
  VStack,
} from "@chakra-ui/react";
import React from "react";
function Navbar() {
  const [isLargerThan1000] = useMediaQuery("(min-width: 1000px)");
  const [isLargerThan750px] = useMediaQuery("(min-width: 750px)");
  const [islesserThan740px] = useMediaQuery("(max-width: 750px)");
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();
  if (isLargerThan1000) {
    return (
      <Box p="20px" m="auto" backgroundColor={"#404258"} position="sticky">
        <HStack
          justifyItems={"center"}
          justifyContent={"space-evenly"}
          m="auto"
        >
          <Heading
            fontFamily={"Snell Roundhand, cursive"}
            color="#FED049"
            fontSize="53px"
            cursor={"pointer"}
          >
            SUMIT
          </Heading>
          <Heading
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Home
          </Heading>
          <Heading
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            About Me
          </Heading>
          <Heading
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Skills
          </Heading>
          <Heading
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Project
          </Heading>
          <Heading
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Contact{" "}
          </Heading>
          <Heading
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Resume{" "}
          </Heading>
        </HStack>
      </Box>
    );
  } else if (isLargerThan750px) {
    return (
      <Box p="20px" m="auto"  backgroundColor={"#404258"} position="sticky">
        <HStack
          justifyItems={"center"}
          justifyContent={"space-evenly"}
          m="auto"
        >
          <Heading
            size={"lg"}
            fontFamily={"Snell Roundhand, cursive"}
            color="#FED049"
            fontSize="33px"
            cursor={"pointer"}
          >
            SUMIT
          </Heading>
          <Heading
            size={"lg"}
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Home
          </Heading>
          <Heading
            size={"lg"}
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            About Me
          </Heading>
          <Heading
            size={"lg"}
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Skills
          </Heading>
          <Heading
            size={"lg"}
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Project
          </Heading>
          <Heading
            size={"lg"}
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Contact{" "}
          </Heading>
          <Heading
            size={"lg"}
            color={"#FAF7F0"}
            fontFamily={"American Typewriter, serif"}
            cursor={"pointer"}
          >
            Resume{" "}
          </Heading>
        </HStack>
      </Box>
    );
  } else if (islesserThan740px) {
    return (
      <Box p="20px" m="auto" backgroundColor={"#404258"} position="sticky">
        <HStack
          justifyItems={"center"}
          justifyContent={"space-between"}
          m="auto"
        >
          <Heading
            fontFamily={"Snell Roundhand, cursive"}
            color="#FAF7F0"
            fontSize="53px"
            cursor={"pointer"}
            ml="50px"
          >
            SUMIT
          </Heading>
          <Button ref={btnRef} colorScheme="teal" onClick={onOpen}>
          <HamburgerIcon/>
          </Button>
          <Drawer
            isOpen={isOpen}
            placement="right"
            onClose={onClose}
            finalFocusRef={btnRef}
          >
            <DrawerOverlay />
            <DrawerContent backgroundColor={"#FDFDBD"}  >
              <DrawerCloseButton />
              <DrawerHeader >
                <Heading fontFamily={"American Typewriter, serif"} cursor={"pointer"} size={"lg"} textAlign={"center"}>Menu</Heading>
              </DrawerHeader>

              <DrawerBody>
               <VStack M="75%" P="5%" m={"auto"} mt="30px" justifyContent="space-evenly" spacing={"30px"} alignItems={"center"}>
               <Heading color={"#557153"}  fontFamily={"American Typewriter, serif"} cursor={"pointer"} size={"lg"} >HOME</Heading>
                <Heading color={"#557153"} fontFamily={"American Typewriter, serif"} cursor={"pointer"} size={"lg"}>ABOUTME</Heading>
                <Heading color={"#557153"} fontFamily={"American Typewriter, serif"} cursor={"pointer"} size={"lg"}>SKILLS</Heading>
                <Heading color={"#557153"} fontFamily={"American Typewriter, serif"} cursor={"pointer"} size={"lg"}>CONTACT</Heading>
                <Heading color={"#557153"} fontFamily={"American Typewriter, serif"} cursor={"pointer"} size={"lg"}>RESUME</Heading>
               </VStack>
              </DrawerBody>
            </DrawerContent>
          </Drawer>
        </HStack>
      </Box>
    );
  }
}

export default Navbar;
